.card__stars {
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
  align-items: flex-end;
  text-align: right;
}

.stars__amount {
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

.stars-stars {
  display: flex;
}

.stars-stars__star {
  background: url("./images/star.svg") center no-repeat;
  height: 16px;
  width: 16px;
  margin-right: 4px;
}

.stars-stars--1 :nth-child(-n + 1),
.stars-stars--2 :nth-child(-n + 2),
.stars-stars--3 :nth-child(-n + 3),
.stars-stars--4 :nth-child(-n + 4),
.stars-stars--5 :nth-child(-n + 5) {
  background-image: url("./images/star-active.svg");
}
